// #region cache the needed dom Elements.
const heroSec = document.querySelector('.sec-hero');
const links = window.document.querySelectorAll('a');
const togglerBtn = window.document.querySelector('.toggler-btn');
const headerContent = window.document.querySelector('.header__content');
const currentYearElements = window.document.querySelectorAll('.current-year');
const scrollToTopBtn = window.document.querySelector('.scroll-to-top-btn');

const modal = window.document.querySelector('.modal');
const modalCloseBtn = window.document.querySelector('.modal__close-btn');
const tourCardBtns = window.document.querySelectorAll('.tour-card__btn');
const modalBookBtn = window.document.querySelector('.modal__book-btn');

const scrollToBottomBtn = window.document.querySelector(
  '.scroll-to-bottom-btn'
);
const sections = window.document.querySelectorAll('.sec');
// #endregion

// #region modal functionality
const openModal = () => modal.classList.add('show');
const closeModal = () => modal.classList.remove('show');
tourCardBtns.forEach((btn) => btn.addEventListener('click', openModal));
modalCloseBtn.addEventListener('click', closeModal);
modal.addEventListener('click', (e) => e.target === modal && closeModal());
modalBookBtn.addEventListener('click', closeModal);
// #endregion

// #region  Discover Button functionality
const goToSec = (section, timeInSec) => {
  window.setTimeout(() => {
    section.scrollIntoView({ behavior: 'smooth' });
  }, timeInSec);
};

scrollToBottomBtn.addEventListener('click', (e) => {
  const SECTION_DISCOVER_TIME = 2000;
  sections.forEach((sec, index) => goToSec(sec, SECTION_DISCOVER_TIME * index));
  goToSec(heroSec, sections.length * SECTION_DISCOVER_TIME);
});
// #endregion

// #region custome radio button  functionality
const radioControlBtns = window.document.querySelectorAll(
  '.radio-control__btn'
);

radioControlBtns.forEach((btn) => {
  btn.addEventListener('click', () => {
    const radioInput = btn.previousElementSibling;
    radioInput.checked = !radioInput.checked;
  });
});
// #endregion

// #region  navbar toggler buttom functionality
togglerBtn.addEventListener('click', () => {
  headerContent.classList.toggle('open');
  window.setTimeout(() => {
    togglerBtn.blur();
  }, 1000);
});
// #endregion

// #region handle smooth scrolling in internal navigation
links.forEach((link) => {
  link.addEventListener('click', (e) => {
    const href = link.getAttribute('href');

    if (href === '#') {
      // Scroll back to top
      e.preventDefault();
      window.scrollTo({ top: 0, behavior: 'smooth' });
    } else if (href.lenght !== 1 && href.startsWith('#')) {
      // Scroll to other internal section
      e.preventDefault();
      const sectionEl = document.querySelector(href);

      // sectionEl.scrollIntoView({ behavior: 'smooth' });
      window.scrollTo({
        top: `${sectionEl.offsetTop}`,
        behavior: 'smooth',
      });
    } else {
      // Scroll to others target
      // do normall behavior
    }

    window.setTimeout(() => {
      link.blur();
    }, 1000);

    // Close nav Linst Layer in case small screen
    if (headerContent.classList.contains('open')) {
      headerContent.classList.remove('open');
    }
  });
});
// #endregion

// #region  Update Current year functionality
const currentYear = new Date().getFullYear();
currentYearElements.forEach((ele) => {
  ele.textContent = currentYear;
});
// #endregion

// #region handle scroll to top button functionality
scrollToTopBtn.addEventListener('click', () => {
  window.scrollTo({ top: 0, behavior: 'smooth' });
  window.setTimeout(() => {
    scrollToTopBtn.blur();
  }, 1000);
});

//  show & hidden  scroll to top button Using : Intersection Observer API
const obs = new IntersectionObserver(
  (entries) => {
    const ent = entries[0];

    if (ent.isIntersecting === false) {
      scrollToTopBtn.classList.add('show');
    } else {
      scrollToTopBtn.classList.remove('show');
    }
  },
  {
    // In the viewport
    root: null,
    threshold: 0,
  }
);
obs.observe(heroSec);
// #endregion
